import React ,{Component}from 'react';
import { Card,Accordion ,Button,Badge} from 'react-bootstrap';
import axios from 'axios';


class All_states  extends Component{

  constructor(){

    super();
    this.state={
      stateData:{}
    }
  }
   
    componentDidMount(){
    
        axios.get("https://api.covid19india.org/state_district_wise.json").then(res =>{
       
         this.setState({stateData:res.data});
         
         
         
      });
      }

    
    render(){
    
           
             let keys=Object.keys(this.state.stateData);
             
             


               return(  
               
                
                    
               <div className="pt-5">
                 <h1 className="p-5">STATEWISE DATA IN INDIA</h1>
            
               
    <Accordion className="p-5" defaultActiveKey="0">

  

    {
        
        keys.map((itm,ky)=>{
          let districts=this.state.stateData[itm].districtData;
          let district_keys=Object.keys(districts);

         let total_active=0;
         let total_confirmed=0;
         let total_deaths=0;
         let total_recovered=0;

         let district_list=[];
         var x=0;
         for(x in districts){

          total_active+=districts[x].active;
          total_confirmed+=districts[x].confirmed;
          total_deaths+=districts[x].deaths;
          total_recovered+=districts[x].recovered;
          let ob=districts[x];
          ob["district_name"]=x;
          district_list.push(ob)
         }

         

return(
          

  
  <Card>
    <Card.Header>
      <Accordion.Toggle as={Button} variant="link" eventKey={ky}>
      <h5>
      <Badge variant="info">{itm}</Badge> <span><Badge variant="warning">total active-{total_active}</Badge></span><span className="pl-3"><Badge variant="warning">total confirmed-{total_confirmed}</Badge></span><span className="pl-3"><Badge variant="warning">total recovered-{total_recovered}</Badge></span>
      </h5>
     
      
     
      </Accordion.Toggle>
    </Card.Header>
    <Accordion.Collapse eventKey={ky}>
      <Card.Body>
        <table className="table table-bordered  table-responsive ">
            <thead>
                    <tr>
                      <td>Districts</td>
                      <td>Confirmed</td>
                      <td>Active</td>
                      <td>Recovered</td>
                      <td>Death</td>
                    </tr>

            </thead>
            <tbody>
              {

             district_list.map((itm,ky)=>{
                      
                      
                     
                    return(
                  <tr>
                     <td>{itm.district_name}</td>
                    <td>{itm.confirmed}</td>
                     <td>{itm.active}</td>
                     <td>{itm.recovered}</td>
                     <td>{itm.deceased}</td>




                  </tr>
                    )
      })

              }

            </tbody>




        </table>




      </Card.Body>
    </Accordion.Collapse>
  </Card>

        )
        





        })



    }
     
  
</Accordion>
             
         </div>         
                 
               )
    }     
                
               
                 

}
export default All_states;